.card {
  box-sizing: border-box;
  position: relative;
  margin: 0.5rem 0 1rem 0;
  border-radius: 2px;
  background-color: #fff;
}
.card .card-content {
  max-height: 100%;
  overflow: hidden;
  padding: 24px;
}
.card .card-image {
  position: relative;
}
.card .card-image img {
  display: block;
  border-radius: 2px 2px 0 0;
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
}
.card .card-image .card-title {
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  max-width: 100%;
  padding: 24px;
}
.card .card-action {
  background-color: inherit;
  position: relative;
  padding: 16px 24px;
}
.card .card-action a:not(.btn):not(.btn-large) {
  color: #ffab40;
  margin-right: 24px;
  transition: color 0.3s ease;
  text-transform: uppercase;
}
.card .card-action a:not(.btn):not(.btn-large):hover {
  color: #ffd180;
}

.form-control {
  box-sizing: border-box;
}
.form-control input, .form-control textarea {
  box-sizing: border-box;
  background-color: inherit;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  width: 100%;
  border-radius: 5px;
  border: 1px solid rgb(118, 118, 118);
}
.form-control label {
  display: block;
}
.form-control textarea {
  width: 100%;
}

.form-control-m {
  display: inline-flex;
  position: relative;
}
.form-control-m input, .form-control-m textarea {
  padding-left: 1rem;
  background-color: inherit;
  padding-right: 1rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  color: #333;
}
.form-control-m label {
  color: #666;
  display: block;
  position: absolute;
  top: 0;
  transition: all 0.2s ease-in-out;
}
.form-control-m textarea {
  box-sizing: border-box;
  width: 100%;
}

.outlined-input-m {
  color: #333;
}

.filled-input-m {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  background-color: #999;
}

.standard-input-m {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: 2px solid rgb(118, 118, 118);
}

.standard-input-m:not(:-moz-placeholder-shown) + label {
  transform: translate(0, -12px) scale(0.8);
}

.standard-input-m:not(:placeholder-shown) + label, .form-control-m:focus-within label {
  transform: translate(0, -12px) scale(0.8);
}

.standard-input-m:focus {
  outline: none;
  border-bottom: 2px solid #0d6efd;
}

.checkbox {
  height: 1.5rem;
